import React from "react";
import styles from "./spinning-image.module.css";

class SpinningImage extends React.Component {
  constructor(props) {
    super(props);
    this.state = { showImage: true, pause: false, bigger: false };
  }

  onClick(e) {
    e.preventDefault();
    console.log("Clicked");
    this.setState({ ...this.state, showImage: !this.state.showImage });
  }

  onPointer(isDown) {
    this.setState({ ...this.state, pause: isDown });
  }

  render() {
    return (
      <div className={`${styles.container} ${styles.center}`}
        onClick={e => this.onClick(e)}
        onPointerDown={e => this.onPointer(true)}
        onPointerUp={e => this.onPointer(false)}
      >
        <div className={`${styles.center} ${styles.spinning} ${this.state.pause ? styles.pause : ""}`}>
          {this.state.showImage ? (
            <img
              draggable="false" onDragStart={(e) => {e.preventDefault();}}
              src={this.props.image}
              className={`${styles.image}`}
              alt=""
            />
          ) : (
            <span className={`${styles.text} ${this.props.bigger ? styles.bigger : ""}`}>
              {this.props.text}
            </span>
          )}
        </div>
      </div>
    );
  }
}
export default SpinningImage;
